import { useState, useEffect } from 'react';
import Typography from '@mui/material/Typography';

export default function CountdownTimer({ onTimeChange }) {
  const [secondsLeft, setSecondsLeft] = useState(300); // 5 ÂàÜÈêòÂÄíÊï∏

  useEffect(() => {
    if (secondsLeft <= 0) {
      if (onTimeChange) onTimeChange('00:00'); // ÂÄíÊï∏ÁµêÊùü
      return;
    }

    const interval = setInterval(() => {
      setSecondsLeft((prev) => prev - 1);
    }, 1000);

    return () => clearInterval(interval);
  }, [secondsLeft, onTimeChange]);

  const minutes = Math.floor(secondsLeft / 60);
  const seconds = secondsLeft % 60;

  const formattedTime = `${minutes.toString().padStart(2, '0')}:${seconds
    .toString()
    .padStart(2, '0')}`;

  // ÊØèÁßíÂõûÂÇ≥Áµ¶Áà∂ÂÖÉ‰ª∂
  useEffect(() => {
    if (onTimeChange) onTimeChange(formattedTime);
  }, [formattedTime, onTimeChange]);

  return (
    <Typography variant='body1' sx={{ fontWeight: 700, color: '#ff4d4f' }}>
      Ââ©È§òÊôÇÈñì: {formattedTime}
    </Typography>
  );
}
